import BoardTzqx from '@/components/boardTzqx';
import { GetDeviceProperty } from '@/api/devicelist';
export default {
  components: {
    BoardTzqx: BoardTzqx
  },
  data: function data() {
    return {
      isload: false,
      extendData: [],
      tzqx: {}
    };
  },
  mounted: function mounted() {
    var _this = this;

    GetDeviceProperty({
      DeviceId: this.$route.query.id,
      IsExtProperty: 0
    }).then(function (res) {
      res.data.map(function (item) {
        if (item.propertyName == "特征曲线") {
          _this.tzqx = item;
        } else {
          _this.extendData.push(item);
        }
      });
      _this.isload = true;
    });
  }
};